.menu-item {
    min-width: 30%;
    height: 270px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    margin: 5px 7.5px 15px;

    &:hover {
        cursor: pointer;

        & .background-image{
            transform: scale(1.1);
            transition: transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }

        & .content{
            opacity: 0.9;
        }
    }
    

    &.large{
        height: 380px;
    }

    &:first-child {
        margin-right: 7.5px;
    }

    &:last-child {
        margin-right: 7.5px;
    }

    .background-image {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
    }

    .content{
        height: 90px;
        padding: 0 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;
        background-color: #fff;
        opacity: 0.65;
        position: absolute;
    

        .title {
            font-weight: bold;
            margin-bottom: 6px;
            font-size: 24px;
            color: #4a4a4a;
        }
        .subtitle {
            font-weight: lighter;
            font-size: 14px;
        }
    }
}